@import '../base/index.scss';

.swiper {
    max-height: 100vh;
    &__item {
        position: relative;
        height: 650px;
        overflow: hidden;
    }
    &__img{
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position:  center;
        }
    }
    &__content{
        padding: 0 15px;
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title{
        margin-bottom: 40px;
        color: $superLight;
        font-family: $oswaldFont;
        font-weight: 600;
        font-size: rem(50px);
    }
}

@media only screen and (min-width: 414px) {
    .swiper {
        &__title{
            font-size: rem(70px);
        }
    }
}
@media only screen and (min-width: 768px) {
    .swiper {
        &__title{
            font-size: 98px;
        }
    }
}